}

#baseMapSwitchDiv {
  position: absolute;
  top: 152px;
  right: 3.8em;
}

.thumb-menu-basemap {
  height: 35px;
  margin-right: 0.5em;
}

#baseMapSwitchDiv button.btn.dropdown-toggle.btn-light {
